<template>
<div class="container">
  <div>添加文章</div>
  <form @submit.prevent="articleCreate(forms.article.create)">
    <input type="text" placeholder="标题" v-model="forms.article.create.attributes.title" />
    <input type="text" placeholder="内容" v-model="forms.article.create.attributes.content" />
    <input type="submit" value="添加" />
    <input type="button" value="取消" @click="$router.go(-1)"/>
  </form>
</div>
</template>

<script>
import Article from '@/service/Article.js'

var ret={
  data () {
    return {
      init:false,
      source:null,
      forms:{
        article:{
          create:Article.createForm("create")
        }
      }
    }
  },
  methods:{}
};

Article.attach(ret);
export default ret
</script>



